.comments {
  position: relative;

  padding-top: 42px;
  padding-bottom: 70px;
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width: $tablet-width) {
    padding: 0;
    padding-top: 34px;
    padding-bottom: 75px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 187px;

    background-image: url("../img/quote.svg");
    background-repeat: no-repeat;
    background-size: 79px auto;
    background-position: center top 17%;
  }
}

.comments__wrapper {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    justify-content: space-between;
    width: 940px;
    margin: 0 auto;
  }
}

.comments::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;

  width: 320px;
  height: 24px;

  background: linear-gradient(to bottom right, #ffffff 50%, $color-bg 50%);

  @media (min-width: $tablet-width) {
    width: 100%;
    height: 50px;
  }

  @media (min-width: $desktop-width) {
    bottom: -11px;
    height: 81px;
  }
}

.comments__list {
  margin-bottom: 36px;

  @media (min-width: $tablet-width) {
    margin-bottom: 1px;
  }

  @media (min-width: $desktop-width) {
    width: 820px;
  }
}

.comments__author {
  display: block;
  width: 158px;
  margin: 0 auto;
  margin-bottom: 13px;

  font-style: normal;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
  text-align: center;

  @media (min-width: $tablet-width) {
    order: 1;
    width: 185px;

    font-size: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 225px;
    font-size: 24px;
  }
}

.comments__text {
  margin: 0;

  line-height: 30px;
  text-align: center;

  @media (min-width: $tablet-width) {
    margin-bottom: 36px;
    font-size: 18px;
  }

  @media (min-width: $desktop-width) {
    width: 710px;
    margin-bottom: 35px;
  }
}

.comments__item {
  display: none;
  min-height: 223px;
}

.comments__slider {
  align-self: center;
  order: 1;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.comments__slide-1:checked ~ .comments__list .comments__comment-1,
.comments__slide-2:checked ~ .comments__list .comments__comment-2,
.comments__slide-3:checked ~ .comments__list .comments__comment-3 {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}

.comments__slide-1:checked ~ .comments__slider label[for="slide-1"],
.comments__slide-2:checked ~ .comments__slider label[for="slide-2"],
.comments__slide-3:checked ~ .comments__slider label[for="slide-3"] {
  background-color: transparent;
  box-shadow: 0 0 0 3px $color-secondary;
  opacity: 1;
}

.comments__slide-1:focus ~ .comments__slider label[for="slide-1"],
.comments__slide-2:focus ~ .comments__slider label[for="slide-2"],
.comments__slide-3:focus ~ .comments__slider label[for="slide-3"] {
  box-shadow: 0 0 0 3px $color-main;
}

.comments__author-info {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    margin-top: 2px;

    font-size: 14px;
    font-weight: 400;
    color: $color-secondary;
  }

  @media (min-width: $desktop-width) {
    font-size: 18px;
  }
}

.comments__arrow {
  margin-top: 26px;
}

.comments__input {
  @media (min-width: $desktop-width) {
    display: none;
  }
}
